.select {
  margin-left: 20px;
  margin-right: 20px;
  display: inline-block;
  width: auto;
}

.filter {
  margin-bottom: 20px;
}

// table header styles
.header {
  color: #333;
  text-decoration: none;
  white-space: nowrap;
  &:hover,
  &:active,
  &:focus {
    color: #333;
    text-decoration: none;
  }
}

.headerCaret {
  margin-left: 8px;
}

.headerIcon {
  color: darkgrey;
  margin-left: 10px;
}

.percent {
  white-space: nowrap;
  > sup {
    margin-left: 1px;
    margin-right: 1px;
  }
}
